import React from "react";
import "../../App";
import Switch from "@mui/joy/Switch";
import Typography from "@mui/joy/Typography";

const PlatformSettings = () => {
  return (
    <>
      <Typography
        sx={{
          fontFamily: "Poppins",
          fontWeight: "500",
          fontSize: "16px",
          lineHeight: "24px",
          color: "#252F40",
          padding: "10px 0px",
        }}
      >
        Platform Settings
      </Typography>
      <Typography
        sx={{
          fontFamily: "Poppins",
          fontWeight: "500",
          fontSize: "12px",
          lineHeight: "18px",
          color: "#67748E",
          margin: "10px 0px",
        }}
      >
        ACCOUNT
      </Typography>
      <div className="contentGrid">
        <div className="contentcol1">
          <Switch size="sm" />
        </div>
        <div className="contentcol2">Email me when someone follows</div>
        <div className="contentcol1">
          <Switch size="sm" />
        </div>
        <div className="contentcol2">Email me when someone answers</div>
        <div className="contentcol1">
          <Switch size="sm" />
        </div>
        <div className="contentcol2">Email me when someone mentions</div>
      </div>

      <Typography
        sx={{
          fontFamily: "Poppins",
          fontWeight: "500",
          fontSize: "12px",
          lineHeight: "18px",
          color: "#67748E",
          margin: "10px 0px",
        }}
      >
        APPLICATION
      </Typography>
      <div className="contentGrid">
        <div className="contentcol1">
          <Switch size="sm" />
        </div>
        <div className="contentcol2">New launches and projects</div>
        <div className="contentcol1">
          <Switch size="sm" />
        </div>
        <div className="contentcol2">Monthly product update</div>
        <div className="contentcol1">
          <Switch size="sm" />
        </div>
        <div className="contentcol2">subscribe for newslet</div>
      </div>
    </>
  );
};
export { PlatformSettings };
