import React from "react";
import "../../App";
import Typography from "@mui/joy/Typography";
import IconButton from "@mui/joy/IconButton";
import CreateIcon from "@mui/icons-material/Create";
import FacebookOutlinedIcon from "@mui/icons-material/FacebookOutlined";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import Stack from "@mui/joy/Stack";

const ProfileInfo = () => {
  return (
    <>
      <Typography
        endDecorator={
          <IconButton variant="plain" sx={{ position:'absolute',right:'20px',top:'20px'}}>
            <CreateIcon sx={{ height: "15px", color: "#67748E" }} />
          </IconButton>
        }
        sx={{
          fontFamily: "Poppins",
          fontWeight: "500",
          fontSize: "16px",
          lineHeight: "24px",
          color: "#252F40",
          padding: "10px 0px"
        }}
      >
        Profile Information
      </Typography>
      <div
        className="content"
        style={{
          margin: "10px 0px",
        }}
      >
        Hi, I’m Alec Thompson, Decisions: If you can’t decide, the answer is no.
        If two equally difficult paths, choose the one more painful in the short
        term (pain avoidance is creating an illusion of equality).
      </div>
      <hr
        style={{
          width: "100%",
          height: "1px",
          background: "#F0F0F0",
        }}
      />
      <div
        className="content"
        style={{ "marginTop": "20px", color: "#252F40" }}
      >
        <mark>Full Name:&nbsp;&nbsp;&nbsp; </mark>Sarah Emily Jacob
      </div>
      <div
        className="content"
        style={{ "marginTop": "20px", color: "#252F40" }}
      >
        <mark>Mobile:&nbsp;&nbsp;&nbsp; </mark>(44) 123 1234 123
      </div>
      <div
        className="content"
        style={{ "marginTop": "20px", color: "#252F40" }}
      >
        <mark>Email:&nbsp;&nbsp;&nbsp; </mark>sarahjacob@mail.com
      </div>
      <div
        className="content"
        style={{ "marginTop": "20px", color: "#252F40" }}
      >
        <mark>Location:&nbsp;&nbsp;&nbsp; </mark>USA
      </div>
      <Stack direction="row" spacing={0.35} sx={{ marginTop: "20px" }}>
        <mark>Social:&nbsp;&nbsp;&nbsp; </mark>
        <a href="https://www.facebook.com/" target="_blank">
          <FacebookOutlinedIcon sx={{ height: "16px", color: "#344E86" }} />
        </a>
        <a href="https://www.twitter.com/" target="_blank">
          <TwitterIcon sx={{ height: "16px", color: "#3EA1EC" }} />
        </a>
        <a href="https://www.instagram.com/" target="_blank">
          <InstagramIcon sx={{ height: "16px", color: "#E1306C" }} />
        </a>
      </Stack>
    </>
  );
};
export { ProfileInfo };
