import React from "react";
import Card from "@mui/joy/Card";
import { Grid, Divider } from "@mui/material";
import DescriptionSharpIcon from "@mui/icons-material/DescriptionSharp";
import VisibilitySharpIcon from "@mui/icons-material/VisibilitySharp";
import DownloadIcon from "@mui/icons-material/Download";
import DeleteForeverOutlinedIcon from "@mui/icons-material/DeleteForeverOutlined";
import Typography from "@mui/joy/Typography";
import Button from "@mui/joy/Button";
import AddIcon from "@mui/icons-material/Add";
const Documents = () => {
  const files = [
    "ATTACHEMENT 836128956.xml",
    "DF_ASKD_UED_changed_2393.pdf",
    "ATTACHEMENT 836128956.html",
    "Draft1-changes-in.red-from-Simon.json",
  ];
  return (
    <Card variant="outlined" sx={{ p: "20px", marginTop: "20px" }}>
      <div
        style={{
          display: "flex",
          color: "#25B581",
          justifyContent: "space-between",
        }}
      >
        <div
          style={{
            fontFamily: "Poppins",
            fontWeight: "500",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <div
            style={{
              fontSize: " 17px",
              lineHeight: "30px",
              color: "#313033",
            }}
          >
            Documents
          </div>
          <div
            style={{
              fontSize: " 7px",
              lineHeight: "10px",
              color: "#848787",
            }}
          >
            Max file size 25MB
          </div>
        </div>
        <Typography
          sx={{
            fontFamily: "Poppins",
            fontWeight: "500",
            fontSize: "20px",
            lineHeight: "30px",
          }}
        ></Typography>
        <Button
          variant="solid"
          color="success"
          sx={{
            fontFamily: "Poppins",
            fontStyle: "normal",
            fontWeight: "600",
            fontSize: "10px",
            height: "10px",
            // lineHeight: "14px",
            borderRadius: "10px",
            color: "white",
          }}
        >
          Upload
        </Button>
      </div>
      <Grid
        container
        spacing={2}
        justifyContent="center"
        alignItems="center"
        sx={{ p: "10px",marginTop:"5px" }}
      >
        {files.map((file) => (
          <Grid item xs={12}>
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "26px 264px 24px 24px 24px",
                gap: "20px",
              }}
            >
              <div style={{ gridColumn: "1"}}>
                <DescriptionSharpIcon sx={{ height: "24px" }} />
              </div>
              <div
                style={{
                  gridColumn: "2",
                  height: " 15px",
                  fontFamily: "Poppins",
                  fontWeight: "600",
                  fontSize: "12px",
                  lineHeight: "15px",
                  color: "#938F99",
                }}
              >
                {file}
              </div>
              <div style={{ gridColumn: "3" }}>
                <VisibilitySharpIcon sx={{ height: "15px" }} />
              </div>
              <div style={{ gridColumn: "4"}}>
                <DownloadIcon sx={{ height: "15px" }} />
              </div>
              <div style={{ gridColumn: "5"}}>
                <DeleteForeverOutlinedIcon sx={{ height: "15px" }} />
              </div>
            </div>
            {/* Content for the first row */}
            <Divider variant="fullWidth" />
          </Grid>
        ))}
      </Grid>
    </Card>
  );
};

export default Documents;
