import React from "react";
import Card from "@mui/joy/Card";
import Timeline from "@mui/lab/Timeline";
import TimelineItem from "@mui/lab/TimelineItem";
import TimelineContent from "@mui/lab/TimelineContent";
import Typography from "@mui/joy/Typography";
import Box from "@mui/joy/Box";
import TimelineConnector from "@mui/lab/TimelineConnector";
import TimelineSeparator from "@mui/lab/TimelineSeparator";
import TimelineOppositeContent, {
  timelineOppositeContentClasses,
} from "@mui/lab/TimelineOppositeContent";
import Jan22 from "./TimelineComponents/Jan22";
import Dec12 from "./TimelineComponents/Dec12";
import Nov18 from "./TimelineComponents/Nov18";
import Nov4 from "./TimelineComponents/Nov4";
import Button from "@mui/joy/Button";
import KeyboardArrowDownOutlinedIcon from '@mui/icons-material/KeyboardArrowDownOutlined';
import "../../App"
const TimelineTab = () => {
  return (
    <Card variant="outlined" sx={{ p: "20px" }} >
    <Box className="activitiesContent" sx={{fontSize: "20px",color: "#79747E",display:"flex",justifyContent:"space-between"}}>
     <div style={{padding:'10px'}}>Activities</div>
       <Button
          size="sm"
          endDecorator={<KeyboardArrowDownOutlinedIcon/>}
          variant="outlined"
          color="neutral"
          sx={{ padding: "0px 6px", color: "#313033",fontSize: "12px",height:"10px"}}
        >
          All Show
        </Button>
    </Box>
      
      <Timeline
        sx={{
          [`& .${timelineOppositeContentClasses.root}`]: {
            flex: 0.2,
          },
        }}
      >
        <TimelineItem>
          <TimelineOppositeContent className="activitiesContent" sx={{color: "#667085"}}>
            Jan 22
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineConnector sx={{ backgroundColor: "#E4E7EC" }} />
          </TimelineSeparator>
          <TimelineContent>
            <Jan22 />
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent className="activitiesContent" sx={{color: "#667085"}}>
            Dec 12
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineConnector sx={{ backgroundColor: "#667085" }} />
          </TimelineSeparator>
          <TimelineContent>
            <Dec12 />
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent className="activitiesContent" sx={{color: "#667085"}}>
            Nov 18
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineConnector sx={{ backgroundColor: "#E4E7EC" }} />
          </TimelineSeparator>
          <TimelineContent>
            <Nov18 />
          </TimelineContent>
        </TimelineItem>
        <TimelineItem>
          <TimelineOppositeContent className="activitiesContent" sx={{color: "#667085"}}>
            Nov 4
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineConnector sx={{ backgroundColor: "#E4E7EC" }} />
          </TimelineSeparator>
          <TimelineContent>
            <Nov4 />
          </TimelineContent>
        </TimelineItem>
      </Timeline>
    </Card>
  );
};

export default TimelineTab;
