import React from "react";
import Box from "@mui/joy/Box";
import Typography from "@mui/joy/Typography";
import Button from "@mui/joy/Button";
import PlayArrowOutlinedIcon from '@mui/icons-material/PlayArrowOutlined';
import "../../../App";
const Nov18 = () => {
  return (
    <Box
      className="activitiesContent"
      sx={{
        display: "flex",
        flexDirection: "column",
        gap: 1,
        width: "100px",
        marginLeft: "20px",
        marginTop:"20px",
        width: "305px",
        padding: "10px",
      }}
    >
      <Typography>Asked to bring good stuff college</Typography>
    
      <Box
        sx={{
          display: "flex",
          gap: 1.5
        }}
      >
        <Button
          startDecorator={<PlayArrowOutlinedIcon  />}
          variant="outlined"
          color="neutral"
          sx={{ padding: "6px", color: "#F63D68"}}
        >
        Video...
        </Button>
      
      </Box>
    </Box>
  );
};

export default Nov18;
