import React from "react";
import "../App";
import UserTask from "./ActivityCards/UserTask";
import Documents from "./ActivityCards/Documents";
import TimelineTab from "./ActivityCards/TimelineTab";
const Activities = () => {
  return (
    <div className="activitesGrid">
      <div style={{ "gridColumn": 1}}><TimelineTab/></div>
      <div style={{ "gridColumn": 2}}>
        <UserTask />
        <Documents/>
      </div>
    </div>
  );
};

export default Activities;
