import React from 'react';
import Card from "@mui/joy/Card";
import Avatar from "@mui/joy/Avatar";
import AvatarGroup from "@mui/joy/AvatarGroup";
import girl from "../../image/woman.png";
import boy from "../../image/avatar2.png";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import IconButton from "@mui/joy/IconButton";
import Typography from "@mui/joy/Typography";
import Button from "@mui/joy/Button";
import AddIcon from "@mui/icons-material/Add";
import "../../App";
function UserCard(props) {
    return (
      <Card variant="outlined" sx={{ p: "10px", marginTop: "10px" }}>
        <div
          className="userTaskGrid"
          style={{
            "fontFamily": "Poppins",
            "fontStyle": "normal",
            "fontWeight": "500",
            "fontSize": "11px",
            "lineHeight": "10px",
          }}
        >
          <div
            className="userGridCol1"
            style={{ display: "flex", "flexDirection": "column" }}
          >
            <div
              style={{
                "fontSize": "10px",
              }}
            >
              {props.content}
            </div>
            <div>{props.date}</div>
          </div>
          <div className="userGridCol2">
            Due Date
            <br />
            May 22, 2022
          </div>
          <div className="userGridCol3">
            <AvatarGroup>
              <Avatar
                alt="Remy Sharp"
                src={girl}
                sx={{ height: "25px", width: "25px" }}
              />
              <Avatar
                alt="Travis Howard"
                src={boy}
                sx={{ height: "25px", width: "25px" }}
              />
            </AvatarGroup>
          </div>
          <div className="userGridCol4">
            <IconButton variant="plain">
              <MoreVertIcon sx={{ height: "22px", color: "#49454F" }} />
            </IconButton>
          </div>
        </div>
      </Card>
    );
  }
const UserTask = () => {
  return (
    <Card variant="outlined" sx={{ p: "20px" }}>
          <div style={{display:'flex',color:'#25B581','justifyContent': 'space-between'}}>
            <Typography
              sx={{
                fontFamily: "Poppins",
                fontWeight: "500",
                fontSize: "20px",
                lineHeight: "30px",
              }}
            >
              User's Task
            </Typography>
            <Button
              variant="outlined"
              size="md"
              color="success"
              endDecorator={<AddIcon />}
              style={{
                fontFamily: "Poppins",
                fontStyle: "normal",
                fontWeight: "600",
                fontSize: "10px",
                lineHeight: "14px",
                borderRadius:"10px"
              }}
            >
              New Task
            </Button>
          </div>
          <UserCard
            content="Cras placerat ligula ut leo aliquet, quis tincidunt odio
                  molestie."
            date="May 18, 2022"
          />
          <UserCard
            content="Proin at elit vel arcu mattis tincidunt eget sit amet purus."
            date="February 29, 2012"
          />
          <UserCard
            content="Nulla lacinia quam id libero rhoncus, at condimentum nulla lobortis."
            date="October 25, 2019"
          />
        </Card>
  )
}

export default UserTask;